.cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cartItem {
  width: 700px;
  height: 250px;
  display: flex;

  align-items: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 30px;
}

.cartItem img {
  width: 200px;
}

.cartItem .description {
  width: 100%;
  font-size: 30px;
}

.countHandler input {
  width: 40px;
  text-align: center;
  font-weight: bolder;
}

.checkout button {
  width: 150px;
  height: 50px;
  margin: 10px;
  margin-bottom: 50px;
  cursor: pointer;
}
@media (max-width: 750px) {
  .cartItem {
    margin: 10px;
      width: 350px;
      height: 250px;
      display: flex;
  }
    .cartItem img {
      width: 150px;
    }
    .cartItem .description {
      font-size: 20px;
    }
    .countHandler input {
      width: 30px;
      
    }
        
}
